import { Route, Switch } from 'react-router-dom';
import './App.css';
import Converter from './components/convector/Converter';
import Valute from './components/valute/Valute';

function App() {
  return (
    <div className="App">
      
      <Switch>
        <Route path='/' exact component={() => (<Valute />)} />
        <Route path='/converter' exact component={() => (<Converter />)} />
      </Switch>
    </div>
  );
}

export default App;
